import { Drawer } from "antd";
import { Button, Col, Divider, Image, Row, Typography, List } from "dmsui";
import React from "react";
import {
  ContactsTwoTone,
  ScheduleTwoTone,
  ContainerTwoTone,
  ReconciliationTwoTone,
  WarningTwoTone,
  CheckCircleTwoTone,
  CloseCircleTwoTone,
  InfoCircleTwoTone,
} from "@ant-design/icons";
import { GetUserAuth, logout } from "../../../../DjangoService/authnService";
const data = [
  {
    title: "My Profile",
    descr: "Account settings and more",
    icon: ContactsTwoTone,
    color: "#69da87",
  },
  {
    title: "My Messages",
    descr: "Inbox and tasks",
    icon: ContainerTwoTone,
    color: "#d8da69",
  },
  {
    title: "My Activities",
    descr: "Logs and notifications",
    icon: ScheduleTwoTone,
    color: "#da6969",
  },
  {
    title: "My Tasks",
    descr: "latest tasks and projects",
    icon: ReconciliationTwoTone,
    color: "#437bd1",
  },
];
const alert = [
  {
    title: "Warning Alert",
    date: "21.05.2021- 12:45",
    icon: WarningTwoTone,
    color: "#ffc85d",
    bckColor: "#fff8d5",
  },
  {
    title: "Success Alert",
    date: "21.05.2021- 10:00",
    icon: CheckCircleTwoTone,
    color: "#1bc5bd",
    bckColor: "#c9f7f5",
  },
  {
    title: "Error Alert",
    date: "20.05.2021- 23:00",
    icon: CloseCircleTwoTone,
    color: "#f64e60",
    bckColor: "#ffe2e5",
  },
  {
    title: "Informational Alert",
    date: "19.05.2021- 22:00",
    icon: InfoCircleTwoTone,
    color: "#8950fc",
    bckColor: "#eee5ff",
  },
];
export default class Setting extends React.Component {
  state = { visible: false, userDatas: [] };

  showDrawer() {
    this.setState({
      visible: true,
    });
    this.props.visible(false);
  }

  onClose = () => {
    this.setState({
      visible: false,
    });
    this.props.visible(true);
  };
  componentDidMount() {
    this.GetUserAuth();
  }
  async GetUserAuth() {
    try {
      const res = await GetUserAuth();
      this.setState({ userDatas: res.data });
      console.log(this.state.userDatas);
    } catch (error) {
      console.log("app(error)    =>   : ", error);
    }
  }
  render() {
    const { visible, userDatas } = this.state;
    const { profil } = this.props;
    return (
      <>
        <Drawer
          title={
            <div>
              <Typography.Title
                level={4}
                style={{
                  margin: "0",
                  fontSize: "18px",
                  fontWeight: "500",
                  lineHeight: "0.9",
                }}
              >
                {/* <IdcardTwoTone twoToneColor={"#444"} /> */}
                My Profile
              </Typography.Title>{" "}
              <Typography.Text type="secondary" style={{ fontSize: "13px" }}>
                7 message
              </Typography.Text>
            </div>
          }
          placement={"right"}
          width={350}
          closable={false}
          onClose={this.onClose}
          visible={visible}
          key={"right"}
        >
          <Row gutter={[16, 16]} align="top" wrap={false}>
            <Col flex="104px">
              <Image
                width={104}
                height={105}
                src={userDatas.MUTPHOTOS != null ? userDatas.MUTPHOTOS : profil}
                //
                style={{ borderRadius: "3px", objectFit: "cover" }}
              />
            </Col>
            <Col flex="auto">
              <Typography.Title
                level={5}
                style={{ margin: "0px" }}
                ellipsis={true}
              >
                {userDatas.first_name}
                {userDatas.last_name}
              </Typography.Title>

              <Typography.Paragraph style={{ margin: "0px" }} ellipsis={true}>
                {userDatas.MUTPROFID != null && userDatas.MUTPROFID.MPRLIBCOUR}
              </Typography.Paragraph>
              <Typography.Paragraph
                ellipsis={true}
                style={{ marginBottom: "4px" }}
                type="secondary"
              >
                {userDatas.email}
              </Typography.Paragraph>
              <Button
                type="primary"
                // className="trigger"
                onClick={async () => {
                  try {
                    await logout();
                    localStorage.removeItem("access_token");
                    localStorage.removeItem("refresh_token");
                    this.props.goto("signIn");
                  } catch (error) {
                    console.log(error);
                  }
                }}
                style={{ width: "100px" }}
              >
                Logout
              </Button>
            </Col>
          </Row>
          <Divider dashed style={{ margin: "20px 0px 5px" }} />
          <List
            bordered={false}
            itemLayout="horizontal"
            dataSource={data}
            renderItem={(item) => (
              <List.Item style={{ padding: "7px 0px", border: "0px" }}>
                <List.Item.Meta
                  style={{ margin: "0px" }}
                  avatar={
                    <div
                      style={{
                        backgroundColor: "#f4f4f4",
                        padding: "8px 12px",
                        borderRadius: "4px",
                      }}
                    >
                      <item.icon
                        twoToneColor={item.color}
                        style={{ fontSize: "15px" }}
                      />
                    </div>
                  }
                  title={
                    <a href=" " style={{ fontSize: "13px" }}>
                      {item.title}
                    </a>
                  }
                  description={
                    <div style={{ fontSize: "13px", lineHeight: "0" }}>
                      {item.descr}
                    </div>
                  }
                />
              </List.Item>
            )}
          />
          <Divider dashed style={{ margin: "20px 0px 5px" }} />
          <div
            style={{
              margin: "0px 0px 24px",
              // display: "flex",
              // alignItems: "baseline",
            }}
          >
            <Typography.Title
              level={4}
              style={{
                margin: "18px 0px",
                fontSize: "18px",
                fontWeight: "500",
                lineHeight: "0.9",
              }}
            >
              Recent Notifications
            </Typography.Title>
          </div>
          {alert.map((alerte) => (
            <div
              style={{
                display: "flex",
                padding: "17px 6px 12px",
                backgroundColor: alerte.bckColor,
                borderRadius: "2px",
                marginBottom: "6px",
              }}
            >
              <div style={{ padding: "5px 10px 0px 6px" }}>
                <alerte.icon
                  style={{ fontSize: "18px", color: "#ffc85d" }}
                  twoToneColor={alerte.color}
                />
              </div>
              <div
                style={{
                  margin: "0 ",
                }}
              >
                <Typography.Title
                  level={5}
                  style={{
                    margin: "0",
                    fontSize: "15px",
                    fontWeight: "500",
                    lineHeight: "1",
                  }}
                >
                  {alerte.title}
                </Typography.Title>
                <Typography.Text
                  type="secondary"
                  style={{ fontSize: "12px", color: "#b8b8b8" }}
                >
                  {alerte.date}
                </Typography.Text>
              </div>
            </div>
          ))}
        </Drawer>
      </>
    );
  }
}
