import React from "react";
import "antd/lib/style/themes/default.less";
import "antd/dist/antd.less";

import { MemoryRouter, Route, Switch } from "react-router-dom";
import SignIn from "./templates/template1/Pages/SignIn";
import Application from "./templates/template1/Pages/Application";
var createHost = require("cross-domain-storage/host");
var storageHost = createHost([
  {
    origin: "http://localhost:3003",
    allowedMethods: ["get", "set", "remove"],
  },
  {
    origin: process.env.REACT_APP_DOMAINE + ":3003",
    allowedMethods: ["get", "set", "remove"],
  },
  {
    origin: "http://localhost:3002",
    allowedMethods: ["get", "set", "remove"],
  },
  {
    origin: process.env.REACT_APP_DOMAINE + ":3002",
    allowedMethods: ["get", "set", "remove"],
  },
]);
const App = () => {
  const refHistory = React.createRef();
  const goto = (link) => {
    refHistory.current.history.push(link);
  };
  return (
    <>
      <MemoryRouter
        ref={refHistory}
        initialEntries={[
          localStorage.getItem("access_token") === null ? "signIn" : "app",
        ]}
        initialIndex={0}
      >
        <Switch>
          <Route exact path={"signIn"}>
            <SignIn goto={(link) => goto(link)} />
          </Route>
          <Route exact path={"app"}>
            <Application
              goto={(link) => goto(link)}
              logo={process.env.PUBLIC_URL + "/logo.png"}
              userPhoto={process.env.PUBLIC_URL + "/photo.png"}
            />
          </Route>
        </Switch>
      </MemoryRouter>
    </>
  );
};

export default App;
