import {
  domaine,
  ApplicationApiEndPoint,
  loginApiEndPoint,
  logoutApiEndPoint,
  userAuthApiEndPoint,
} from "../config";
import http from "./httpService";
export const login = (loginObject) => {
  return http.post(domaine + "" + loginApiEndPoint, loginObject, {
    headers: {
      Authorization: localStorage.getItem("access_token")
        ? "Bearer " + localStorage.getItem("access_token")
        : null,
      "Content-Type": "application/json",
      accept: "application/json",
    },
  });
};
export const logout = () => {
  return http.post(
    domaine + "" + logoutApiEndPoint,
    {
      refresh: localStorage.getItem("refresh_token"),
    },
    {
      headers: {
        Authorization: localStorage.getItem("access_token")
          ? "Bearer " + localStorage.getItem("access_token")
          : null,
        "Content-Type": "application/json",
        accept: "application/json",
      },
    }
  );
};
export const GetAllApplications = () => {
  // console.log()
  return http.get(domaine + "" + ApplicationApiEndPoint, {
    headers: {
      Authorization: localStorage.getItem("access_token")
        ? "Bearer " + localStorage.getItem("access_token")
        : null,
      "Content-Type": "application/json",
      accept: "application/json",
    },
  });
};
export const GetAllDisableApplications = () => {
  return http.get(domaine + "DMERP/v1/applications/all/", {
    headers: {
      Authorization: localStorage.getItem("access_token")
        ? "Bearer " + localStorage.getItem("access_token")
        : null,
      "Content-Type": "application/json",
      accept: "application/json",
    },
  });
};
export const GetUserAuth = () => {
  return http.get(
    domaine + "" + userAuthApiEndPoint,
    // { ind: false },
    {
      headers: {
        Authorization: localStorage.getItem("access_token")
          ? "Bearer " + localStorage.getItem("access_token")
          : null,
        "Content-Type": "application/json",
        accept: "application/json",
      },
    }
  );
};
export const verifyToken = (token) => {
  return http.post(
    domaine + "DMERP/v1/auth/token/verify/",
    { token: token },
    {
      headers: {
        // Authorization: localStorage.getItem("access_token")
        //   ? "Bearer " + localStorage.getItem("access_token")
        //   : null,
        "Content-Type": "application/json",
        accept: "application/json",
      },
    }
  );
};
export const RefrechToken = (RefrechToken) => {
  return http.post(
    domaine + "DMERP/v1/auth/token/refresh/",
    { refresh: RefrechToken },
    {
      headers: {
        // Authorization: localStorage.getItem("access_token")
        //   ? "Bearer " + localStorage.getItem("access_token")
        //   : null,
        "Content-Type": "application/json",
        accept: "application/json",
      },
    }
  );
};
export const connexion = async (token, refresh_token, logout) => {
  var verify = false;
  var refresh = false;
  // this.props.goto("signIn");

  try {
    verify = await verifyToken(token);
    console.log("tkoen verify result : ", verify);
  } catch (error) {
    console.log("tkoen verify error : ", error, verify);
    try {
      if (error == "Error: Request failed with status code 401") {
        refresh = await RefrechToken(refresh_token);

        localStorage.setItem("access_token", refresh.data.access);
        localStorage.setItem("refresh_token", refresh.data.refresh);
        console.log("tkoen refresh result : ", refresh);
        // this.props.goto("app");
        window.location.reload();
      }
    } catch (error) {
      if (error == "Error: Request failed with status code 401") {
        console.log("tkoen refresh error : ", error, refresh);

        localStorage.removeItem("access_token");
        localStorage.removeItem("refresh_token");
        logout();
      }
    }
  }
  return verify;
};
