import {
  Avatar,
  Badge,
  Button,
  Col,
  Image,
  Input,
  Layout,
  Row,
  Space,
  Typography,
} from "antd";

import {
  MessageOutlined,
  UserOutlined,
  BellOutlined,
  SearchOutlined,
  SettingOutlined,
  MessageTwoTone,
  BellTwoTone,
} from "@ant-design/icons";
import React, { Component } from "react";
import Setting from "./Setting";
import Compte from "./Compte";
import { Popover } from "dmsui";
import Message from "./Message";
import Notification from "./Notification";
import { GetUserAuth } from "../../../../DjangoService/authnService";
const text = (
  <div
    style={{
      fontSize: "16px",
      padding: "10px",
      fontWeight: "600",
      color: "rgb(37 32 69)",
    }}
  >
    <MessageTwoTone twoToneColor="#0a304e" /> Message
  </div>
);

export default class DMSHeader extends Component {
  constructor(props) {
    super(props);
    this.settingRef = React.createRef();
    this.compteRef = React.createRef();
    this.state = {
      drawarSetting: true,
      userDatas: [],
    };
  }
  componentDidMount() {
    this.GetUserAuth();
  }
  async GetUserAuth() {
    try {
      const res = await GetUserAuth();
      this.setState({ userDatas: res.data });
    } catch (error) {
      console.log("app(error)    =>   : ", error);
    }
  }
  render() {
    const { onSearch, userPhoto, goto } = this.props;
    const { userDatas } = this.state;
    return (
      <>
        <Setting
          ref={this.settingRef}
          visible={(valeur) => {
            this.setState({ drawarSetting: valeur });
            this.props.Drawervisible(valeur);
          }}
        />
        <Compte
          goto={(link) => goto(link)}
          ref={this.compteRef}
          profil={userPhoto}
          visible={(valeur) => {
            // this.setState({ drawarSetting: valeur });
            // this.props.Drawervisible(valeur);
          }}
        />
        <Layout.Header
          className="header"
          style={{
            padding: "0px",
            paddingRight: this.state.drawarSetting ? "8px" : "358px",
            position: "fixed",
            zIndex: 3,
            height: "48px",
            lineHeight: "48px",
            width: "100%",
          }}
        >
          <Row justify="space-between" align="middle" wrap={false}>
            <Col
              flex="80px"
              style={{
                display: "flex",
                justifyContent: "center",
                // width: "500px",
              }}
            >
              <svg
                version="1.1"
                style={{
                  width: "28px",
                  height: "28px",
                }}
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 100 110.6"
              >
                <g fill="#fff">
                  <path
                    d="M91,38.8L53.8,1.6c-2.1-2.1-5.5-2.1-7.6,0l-8.5,8.5l41,41c5.2,5.2,5.2,13.7,0,19L59.5,89.2
		c-5.2,5.2-13.7,5.2-19,0L21.3,70c-5.2-5.2-5.2-13.7,0-19l16.3-16.3L25.3,22.4L9,38.8c-12,12-12,31.6,0,43.6l19.2,19.2
		c12,12,31.6,12,43.6,0L91,82.4C103,70.3,103,50.8,91,38.8z"
                  />
                  <path
                    d="M52.5,70l8.1-8.1c1.2-1.2,1.2-3.1,0-4.3L50.4,47.5L40.2,57.7c-1.2,1.2-1.2,3.1,0,4.3l8.1,8.1
		C49.4,71.1,51.3,71.1,52.5,70z"
                  />
                </g>
              </svg>
            </Col>{" "}
            <Col
              flex="auto"
              style={{
                display: "flex",
                justifyContent: "start",
                // paddingLeft: "20px",
              }}
            >
              <div style={{ width: "539px" }}>
                <Input
                  prefix={<SearchOutlined style={{ color: "#bbb" }} />}
                  placeholder="Search"
                  enterButton
                  onSearch={onSearch}
                />
              </div>
            </Col>
            <Col flex="auto">
              <Row align="top" justify="end">
                <Space size={0}>
                  <Button
                    type="link"
                    icon={<SettingOutlined className={"header-icon-color"} />}
                    size={"large"}
                    onClick={() => {
                      this.settingRef.current.showDrawer();
                      console.log("", this.settingRef.current.state.visible);
                      this.setState({
                        drawarSetting: this.settingRef.current.state.visible,
                      });
                    }}
                    className="trigger"
                  />
                  <Popover
                    className="header-menu"
                    placement="bottomLeft"
                    title={text}
                    content={<Message userPhoto={userPhoto} />}
                    trigger="click"
                  >
                    <Button
                      type="link"
                      icon={
                        <Badge
                          className="site-badge-count-109"
                          count={7}
                          size="small"
                          style={{ backgroundColor: "#52c41a" }}
                        >
                          <MessageOutlined className={"header-icon-color"} />
                        </Badge>
                      }
                      size={"large"}
                      className="trigger"
                    />{" "}
                  </Popover>
                  {/* <Dropdown overlay={MenuUser} trigger={["click"]}> */}
                  <Popover
                    className="header-menu"
                    placement="bottomLeft"
                    title={
                      <div
                        style={{
                          fontSize: "16px",
                          padding: "10px",
                          fontWeight: "600",
                          color: "rgb(37 32 69)",
                        }}
                      >
                        <BellTwoTone twoToneColor="#0a304e" /> Notifications
                      </div>
                    }
                    content={<Notification />}
                    trigger="click"
                  >
                    {" "}
                    <Button
                      type="link"
                      icon={
                        <Badge size="small" count={5}>
                          <BellOutlined className={"header-icon-color"} />
                        </Badge>
                      }
                      size={"large"}
                      className="trigger"
                    />
                  </Popover>
                  {/* </Dropdown> */}
                  <Typography.Text
                    className={"header-icon-color"}
                    style={{
                      paddingRight: "12px",
                      paddingLeft: "8px",
                    }}
                  >
                    HI! {userDatas.username}
                    {/* {localStorage.getItem("userName")} */}
                  </Typography.Text>
                  <Badge
                    status="success"
                    offset={[-5, 29]}
                    className="template-dms-badge template-avatar"
                  >
                    <Avatar
                      src={
                        <Image
                          preview={false}
                          src={
                            userDatas.MUTPHOTOS != null
                              ? userDatas.MUTPHOTOS
                              : userPhoto
                          }
                        />
                      }
                      size={32}
                      onClick={() => {
                        this.compteRef.current.showDrawer();
                        // console.log("", this.settingRef.current.state.visible);
                        // this.setState({
                        //   drawarSetting: this.settingRef.current.state.visible,
                        // });
                      }}
                      icon={<UserOutlined />}
                    />
                  </Badge>
                </Space>
              </Row>
            </Col>
          </Row>
        </Layout.Header>
      </>
    );
  }
}
