import { List, Button } from "antd";
import React from "react";

const count = 3;
const fakeDataUrl = {
  results: [
    {
      gender: "female",
      name: { title: "Miss", first: "Milla", last: "Koskinen" },
      email: "milla.koskinen@example.com",
      nat: "FI",
    },
  ],
};

export default class Theme extends React.Component {
  state = {
    initLoading: true,
    loading: false,
    data: [],
    list: [],
  };

  componentDidMount() {
    this.getData((res) => {
      this.setState({
        initLoading: false,
        data: res.results,
        list: res.results,
      });
    });
  }

  getData = (callback) => {
    callback(fakeDataUrl);
  };

  onLoadMore = () => {
    this.setState({
      loading: true,
      list: this.state.data.concat(
        [...new Array(count)].map(() => ({ loading: true, name: {} }))
      ),
    });
    this.getData((res) => {
      const data = this.state.data.concat(res.results);
      this.setState(
        {
          data,
          list: data,
          loading: false,
        },
        () => {
          // Resetting window's offsetTop so as to display react-virtualized demo underfloor.
          // In real scene, you can using public method of react-virtualized:
          // https://stackoverflow.com/questions/46700726/how-to-use-public-method-updateposition-of-react-virtualized
          window.dispatchEvent(new Event("resize"));
        }
      );
    });
  };

  render() {
    const { initLoading, loading, list } = this.state;
    const loadMore =
      !initLoading && !loading ? (
        <div
          style={{
            textAlign: "center",
            marginTop: 12,
            height: 32,
            lineHeight: "32px",
          }}
        >
          <Button onClick={this.onLoadMore}>loading more</Button>
        </div>
      ) : null;

    return (
      <List
        className="demo-loadmore-list"
        loading={initLoading}
        itemLayout="horizontal"
        loadMore={loadMore}
        dataSource={list}
        renderItem={(item) => (
          <>
            <div
              style={{
                display: "flex",
                justifyContent: "space-around",
                marginBottom: "10px",
              }}
            >
              {" "}
              <button
                style={{
                  margin: "0px 5px",
                  width: "20%",
                  height: "30px",
                  backgroundColor: "#4f448d",
                }}
              ></button>
              <div
                style={{
                  width: "20%",
                  margin: "0px 5px",
                  height: "30px",
                  backgroundColor: "#44718d",
                }}
              ></div>{" "}
              <div
                style={{
                  width: "20%",
                  margin: "0px 5px",
                  height: "30px",
                  backgroundColor: "#40a875",
                }}
              ></div>
              <div
                style={{
                  width: "20%",
                  margin: "0px 5px",
                  height: "30px",
                  backgroundColor: "#a84077",
                }}
              ></div>
              <div
                style={{
                  width: "20%",
                  margin: "0px 5px",
                  height: "30px",
                  backgroundColor: "#bcb540",
                }}
              ></div>
            </div>{" "}
            <div
              style={{
                display: "flex",
                justifyContent: "space-around",
                marginBottom: "10px",
              }}
            >
              {" "}
              <div
                style={{
                  margin: "0px 5px",
                  width: "20%",
                  height: "30px",
                  backgroundColor: "#cf4444",
                }}
              ></div>
              <div
                style={{
                  width: "20%",
                  margin: "0px 5px",
                  height: "30px",
                  backgroundColor: "#424ad3",
                }}
              ></div>{" "}
              <div
                style={{
                  width: "20%",
                  margin: "0px 5px",
                  height: "30px",
                  backgroundColor: "#d3d242",
                }}
              ></div>
              <div
                style={{
                  width: "20%",
                  margin: "0px 5px",
                  height: "30px",
                  backgroundColor: "#42d0d3",
                }}
              ></div>
              <div
                style={{
                  width: "20%",
                  margin: "0px 5px",
                  height: "30px",
                  backgroundColor: "#5cd342",
                }}
              ></div>
            </div>
          </>
          //   <List.Item
          //     actions={[
          //       <a key="list-loadmore-edit">edit</a>,
          //       <a key="list-loadmore-more">more</a>,
          //     ]}
          //   >
          //     <Skeleton avatar title={false} loading={item.loading} active>
          //       <List.Item.Meta
          //         avatar={
          //           <Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />
          //         }
          //         title={<a href="https://ant.design">{item.name.last}</a>}
          //         description="Ant Design, a design language for background applications, is refined by Ant UED Team"
          //       />
          //       <div>content</div>
          //     </Skeleton>
          //   </List.Item>
        )}
      />
    );
  }
}
