import { List, Button } from "antd";
import React from "react";

const count = 3;
const fakeDataUrl = {
  results: [
    {
      gender: "female",
      name: { title: "Miss", first: "Milla", last: "Koskinen" },
      email: "milla.koskinen@example.com",
      nat: "FI",
    },
  ],
};

export default class Theme extends React.Component {
  state = {
    initLoading: true,
    loading: false,
    data: [],
    list: [],
  };

  componentDidMount() {
    this.getData((res) => {
      this.setState({
        initLoading: false,
        data: res.results,
        list: res.results,
      });
    });
  }

  getData = (callback) => {
    callback(fakeDataUrl);
  };

  onLoadMore = () => {
    this.setState({
      loading: true,
      list: this.state.data.concat(
        [...new Array(count)].map(() => ({ loading: true, name: {} }))
      ),
    });
    this.getData((res) => {
      const data = this.state.data.concat(res.results);
      this.setState(
        {
          data,
          list: data,
          loading: false,
        },
        () => {
          // Resetting window's offsetTop so as to display react-virtualized demo underfloor.
          // In real scene, you can using public method of react-virtualized:
          // https://stackoverflow.com/questions/46700726/how-to-use-public-method-updateposition-of-react-virtualized
          window.dispatchEvent(new Event("resize"));
        }
      );
    });
  };

  render() {
    const { initLoading, loading, list } = this.state;
    const loadMore =
      !initLoading && !loading ? (
        <div
          style={{
            textAlign: "center",
            marginTop: 12,
            height: 32,
            lineHeight: "32px",
          }}
        >
          <Button onClick={this.onLoadMore}>loading more</Button>
        </div>
      ) : null;

    return (
      <List
        className="demo-loadmore-list"
        loading={initLoading}
        itemLayout="horizontal"
        loadMore={loadMore}
        dataSource={list}
        renderItem={(item) => (
          <>
            {" "}
            <div
              style={{
                display: "flex",
                justifyContent: "space-around",
                marginBottom: "10px",
              }}
            >
              {" "}
              <div
                style={{
                  width: "30%",
                  height: "50px",
                  backgroundColor: "#777",
                }}
              >
                <img
                  src={process.env.PUBLIC_URL + "/defaultBck.png"}
                  alt=""
                  style={{ width: "100%", height: "100%" }}
                />
              </div>
              <div
                style={{
                  width: "30%",
                  height: "50px",
                  backgroundColor: "#777",
                }}
              >
                {" "}
                <img
                  src={process.env.PUBLIC_URL + "/bck (5).png"}
                  alt=""
                  style={{ width: "100%", height: "100%" }}
                />
              </div>
              <div
                style={{
                  width: "30%",
                  height: "50px",
                  backgroundColor: "#777",
                }}
              >
                {" "}
                <img
                  src={process.env.PUBLIC_URL + "/bck1.png"}
                  alt=""
                  style={{ width: "100%", height: "100%" }}
                />
              </div>{" "}
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "space-around",
                marginBottom: "10px",
              }}
            >
              {" "}
              <div
                style={{
                  width: "30%",
                  height: "50px",
                  backgroundColor: "#777",
                }}
              >
                <img
                  src={process.env.PUBLIC_URL + "/bck (3).png"}
                  alt=""
                  style={{ width: "100%", height: "100%" }}
                />
              </div>
              <div
                style={{
                  width: "30%",
                  height: "50px",
                  backgroundColor: "#777",
                }}
              >
                {" "}
                <img
                  src={process.env.PUBLIC_URL + "/bck (4).png"}
                  alt=""
                  style={{ width: "100%", height: "100%" }}
                />
              </div>
              <div
                style={{
                  width: "30%",
                  height: "50px",
                  backgroundColor: "#777",
                }}
              >
                {" "}
                <img
                  src={process.env.PUBLIC_URL + "/bkg.jpg"}
                  alt=""
                  style={{ width: "100%", height: "100%" }}
                />
              </div>{" "}
            </div>
          </>
        )}
      />
    );
  }
}
