import { Button, Col, Form, Input, Row } from "antd";
import React, { Component } from "react";
import {
  LockOutlined,
  UserOutlined,
  EyeTwoTone,
  EyeInvisibleOutlined,
} from "@ant-design/icons";
import { login } from "../../../DjangoService/authnService";

export default class SignIn extends Component {
  constructor(props) {
    super(props);

    this.state = {
      password: "",
      email: "",
      showPassword: false,
    };
  }
  onFinish = async (values) => {
    console.log(values);
    try {
      const res = await login({
        email: values.username,
        password: values.password,
        // is_desktop: false,
        // est_caisse: false,
        // is_pos: false,
        // terminal: 1,
      });
      localStorage.setItem("access_token", res.data.access_token);
      localStorage.setItem("refresh_token", res.data.refresh_token);
      localStorage.setItem("userName", res.data.user.username);
      localStorage.setItem("email", res.data.user.email);
      localStorage.setItem("profile", res.data.user.MUTPROFID.MPRLIBCOUR);
      localStorage.setItem("id_user", res.data.user.id);
      localStorage.setItem(
        "name",
        res.data.user.first_name !== "" || res.data.user.last_name !== ""
          ? res.data.user.first_name + " " + res.data.user.last_name
          : null
      );
      window.sessionStorage.setItem("key", "value");

      this.props.goto("app");
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    return (
      <div
        style={{
          height: "100%",
          position: "relative",
          width: "100%",
        }}
      >
        <img
          src={process.env.PUBLIC_URL + "/backgraund.png"}
          alt=""
          style={{
            width: "100%",
            height: "100%",
            position: "absolute",
            right: 0,
          }}
        />
        <div
          style={{
            zIndex: "2",
            position: "absolute",
            right: "97%",
          }}
        ></div>
        <div
          style={{
            height: "100%",
            width: "100%",

            display: "flex",
            alignItems: "center",
            justifyContent: "flex-end",
          }}
        >
          <Row
            align="middle"
            justify="space-around"
            style={{
              background: "#fefefe",
              zIndex: "1",
              borderRadius: "10px",
              width: "350px",
              boxShadow: "#cacaca 0px 0px 8px 0px",
              padding: "25px",
              position: "absolute",
              right: "6%",
            }}
          >
            <Col
              span={24}
              style={{ display: "flex", justifyContent: "center" }}
            >
              <div
                style={{
                  margin: "5px 0px 0px",
                }}
              >
                {" "}
                <img src={process.env.PUBLIC_URL + "conforama.png"} alt="" />
              </div>
            </Col>
            <Col
              span={24}
              style={{
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
              }}
            >
              <Form
                name="normal_login"
                className="login-form"
                layout="vertical"
                style={{ marginTop: "25px", width: "100%" }}
                initialValues={{
                  remember: true,
                }}
                onFinish={this.onFinish}
              >
                <Form.Item
                  label="Username"
                  name="username"
                  rules={[
                    {
                      required: true,
                      message: "Please input your Username!",
                    },
                  ]}
                >
                  <Input
                    prefix={<UserOutlined className="site-form-item-icon" />}
                    placeholder="Username"
                  />
                </Form.Item>
                <Form.Item
                  label="Password"
                  name="password"
                  rules={[
                    {
                      required: true,
                      message: "Please input your Password!",
                    },
                  ]}
                >
                  <Input.Password
                    placeholder="Password"
                    prefix={<LockOutlined className="site-form-item-icon" />}
                    iconRender={(visible) =>
                      visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />
                    }
                  />
                </Form.Item>
                <Form.Item>
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "center",
                      margin: "11px 0 5px",
                    }}
                  >
                    <Button
                      type="primary"
                      htmlType="submit"
                      className="login-form-button"
                      style={{ width: "100%" }}
                    >
                      Log in
                    </Button>
                  </div>
                </Form.Item>{" "}
                <Form.Item style={{ margin: "40px 0 0px" }}>
                  <div
                    style={{ display: "flex", justifyContent: "flex-start" }}
                  >
                    <img
                      src={process.env.PUBLIC_URL + "/dataMaster.svg"}
                      alt=""
                      style={{
                        width: "110px",
                        height: "30px",
                      }}
                    />
                  </div>
                </Form.Item>
              </Form>
            </Col>
          </Row>
        </div>
      </div>
    );
  }
}
