import { Drawer } from "antd";
import { Divider, Select, Switch, Typography } from "dmsui";
import React from "react";
import "./mask.css";
import { SettingTwoTone } from "@ant-design/icons";
import Theme from "./Theme";
import Backgraund from "./Backgraund";
export default class Setting extends React.Component {
  state = { visible: false };

  showDrawer() {
    this.setState({
      visible: true,
    });
    this.props.visible(false);
  }

  onClose = () => {
    this.setState({
      visible: false,
    });
    this.props.visible(true);
  };

  render() {
    const { visible } = this.state;
    return (
      <>
        <Drawer
          title={
            <div>
              <Typography.Title
                level={4}
                style={{
                  margin: "0",
                  fontSize: "18px",
                  fontWeight: "500",
                  lineHeight: "0.9",
                }}
              >
                <SettingTwoTone twoToneColor={"#0a304e"} /> Setting
              </Typography.Title>
            </div>
          }
          className="mask"
          placement={"right"}
          closable={false}
          width={350}
          onClose={this.onClose}
          visible={visible}
          key={"right"}
        >
          <div
            style={{
              // margin: "18px 0px",
              marginBottom: "20px",
            }}
          >
            <Typography.Title
              level={4}
              style={{
                // margin: "18px 0px",
                fontSize: "13px",
                fontWeight: "600",
                lineHeight: "0",
                color: "#444",
              }}
            >
              THEME
            </Typography.Title>
          </div>
          <Theme />
          <Divider dashed style={{ margin: "20px 0px " }} />{" "}
          <div
            style={{
              // margin: "18px 0px",
              marginBottom: "20px",
            }}
          >
            <Typography.Title
              level={4}
              style={{
                // margin: "18px 0px",
                fontSize: "13px",
                fontWeight: "600",
                lineHeight: "0",
                color: "#444",
              }}
            >
              BACKGRAUND
            </Typography.Title>
          </div>
          <Backgraund />
          <Divider dashed style={{ margin: "20px 0px " }} />{" "}
          <div
            style={{
              // margin: "18px 0px",
              marginBottom: "20px",
            }}
          >
            <Typography.Title
              level={4}
              style={{
                // margin: "18px 0px",
                fontSize: "13px",
                fontWeight: "600",
                lineHeight: "0",
                color: "#444",
              }}
            >
              MODE
            </Typography.Title>
          </div>{" "}
          <div style={{ display: "flex", justifyContent: "space-around" }}>
            <div
              style={{
                // backgroundImage: `url(${
                //   process.env.PUBLIC_URL + "/darkmode.png"
                // })`,
                // backgroundColor: "#888",

                width: "40%",
                display: "flex",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              <img
                src={process.env.PUBLIC_URL + "/dark.png"}
                alt=""
                style={{
                  width: "100%",
                  height: "80px",
                  border: "2px solid rgb(128 128 128)",
                  borderRadius: "4px",
                  padding: "10px 28px",
                  marginBottom: "17px",
                }}
              />
              <Typography.Title
                level={4}
                style={{
                  // margin: "18px 0px",
                  fontSize: "12px",
                  fontWeight: "600",
                  lineHeight: "0",
                  color: "#444",
                }}
              >
                Dark Mode
              </Typography.Title>
            </div>
            <div
              style={{
                width: "40%",
                display: "flex",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              <img
                src={process.env.PUBLIC_URL + "/light.png"}
                alt=""
                style={{
                  width: "100%",
                  height: "80px",
                  border: "2px solid rgb(128 128 128)",
                  borderRadius: "4px",
                  padding: "10px 28px",
                  marginBottom: "17px",
                }}
              />
              <Typography.Title
                level={4}
                style={{
                  // margin: "18px 0px",
                  fontSize: "12px",
                  fontWeight: "600",
                  lineHeight: "0",
                  color: "#444",
                }}
              >
                {" "}
                Light Mode
              </Typography.Title>
            </div>
          </div>
          <Divider dashed style={{ margin: "20px 0px " }} />{" "}
          <div
            style={{
              // margin: "18px 0px",
              marginBottom: "10px",
              display: "flex",
              alignItems: "baseline",
            }}
          >
            <Typography.Title
              level={4}
              style={{
                // margin: "18px 0px",
                fontSize: "13px",
                fontWeight: "600",
                lineHeight: "0",
                marginRight: "65px",
                color: "#444",
              }}
            >
              LANGUE
            </Typography.Title>
            <Select
              labelInValue
              defaultValue={{ value: "en" }}
              style={{ width: "100%" }}
              // onChange={handleChange}
            >
              {/* nglish (United States) Deutsch (Deutschland) English (United States)
            Español (España, alfabetización internacional) Français (France)
            Affichage */}
              <Select.Option value="en">English (United States)</Select.Option>
              <Select.Option value="fr">Français (France)</Select.Option>
              <Select.Option value="ar">Arabe </Select.Option>
            </Select>
          </div>
          <div
            style={{
              // margin: "18px 0px",
              marginBottom: " 0px",
              display: "flex",
              alignItems: "baseline",
            }}
          >
            <Typography.Title
              level={4}
              style={{
                // margin: "18px 0px",
                fontSize: "13px",
                fontWeight: "600",
                lineHeight: "0",
                marginRight: "65px",
                color: "#444",
              }}
            >
              SON
            </Typography.Title>
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "flex-end",
              }}
            >
              <Switch checkedChildren="on" unCheckedChildren="off" />
            </div>
          </div>
          Faire jouer le son pour les messages entrants et les notifications
        </Drawer>
      </>
    );
  }
}
